import { useState } from "react";
import { Checkbox } from "@mui/material";
import checkedIcon from "../Assets/Images/checkedIcon.svg";

const uncheckedIcon = (
    <svg
  xmlns="http://www.w3.org/2000/svg"
  width="24"
  height="24"
  viewBox="0 0 24 24"
  fill="none"
  stroke="#8A8A8A"
  stroke-width="2"
  stroke-linecap="round"
  stroke-linejoin="round"
>
  <rect x="3" y="3" width="18" height="18" rx="3" stroke="#8A8A8A" fill="none" />
</svg>
  );

export default function CustomCheckbox(props: any) {
  const {label} = props
  const [isChecked, setIsChecked] = useState(false);
  console.log("==>1", isChecked);

  const handleCheckBox = () => {
    console.log("==>", isChecked);
    setIsChecked(!isChecked);
  }

  return (
    <div className="flex items-center" onClick={handleCheckBox}>
      <Checkbox
            color="primary"
            onChange={ handleCheckBox}
            checked={isChecked}
            className=""
            style={{
            color: "#2F80ED",
            }}
            icon={checkedIcon}
            // checkedIcon={checkedIcon}
        />

      <div
        onClick={handleCheckBox}
        className="cursor-pointer ml-2 text-sm text-gray-700 font-poppins"
      >
        {label}
      </div>
    </div>
  );
}
